.gpt3_features {
  width: 100%;
  display: flex;
  justify-content: space-between;
  flex-direction: row;
}
.gpt3_features-heading {
  flex: 1;
  display: flex;
  justify-content: flex-start;
  align-items: flex-start;
  flex-direction: column;

  text-align: left;
  margin-right: 5rem;
}
.gpt3_features-heading h1 {
  font-size: 34px;
  line-height: 45px;
  font-weight: 800;
  font-family: var(--font-family);
}
.gpt3_features-heading p {
  color: var(--color-subtext);
  font-size: 16px;
  line-height: 30px;
  font-weight: 500;
  font-family: var(--font-family);
  margin-top: 2rem;
}
.gpt3_features-container {
  flex: 1.5;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  flex-direction: column;
}
@media screen and (max-width: 990px) {
  .gpt3_features {
      flex-direction: column;
  }
  .gpt3_features-heading {
      margin: 0 0 2rem 0;
  }
}
@media screen and (max-width: 550px) {
  .gpt3_features-heading h1 {
      font-size: 28px;
      line-height: 38px;
  }
}